// 项目中会把 数据的请求单独抽离成到js文件，把对应的方法导出，使用的组件从对应js文件导入

import axios from './config' // ./config/index.js 中index.js 可以省略（只有叫index才能省）

// 请求用户数据
export let getUserData = (page) => axios.get(`/api/userdata?page=` + page)

// 请求商家数据
export let getCustomerData = (page) => axios.get(`/api/customerdata?page=` + page)
// 向商家数据替换新修改的数据
export let postEditData = (editData) => axios.post('/api/editdata',editData)
// 向商家数据删除
export let getDelcusData = (id) => axios.get(`/api/delcusdata?id=` + id)

// 请求食品数据
export let getfoodsData = (page) => axios.get(`/api/foodsdata?page=` + page)
// 向食品数据替换新修改的数据postEditfoodData
export let postEditfoodData = (editData) => axios.post('/api/editfooddata',editData)
// 向食品数据删除 getDelfoodData
export let getDelfoodData = (id) => axios.get(`/api/delfooddata?id=` + id)

// 请求订单数据
export let getOrderData = (page) => axios.get(`/api/orderdata?page=` + page)

// 请求管理员数据
export let getmanData = (page) => axios.get(`/api/mandata?page=` + page)

// 请求食品类别数据
export let getfoodClassData = () => axios.get(`/api/foodClassdata`)

// 请求商品添加中的食品种类接口
export let getfoodTypeData = () => axios.get(`/api/foodTypedata`)

// 添加新商铺 postshopData
export let postshopData = (newData) => axios.post('/api/addshopdata',newData)