<template>
  <div class="manDataman">
    <breadcrumb>
    <span slot="breadone">数据管理&nbsp;/</span>
    <span slot="breadtwo">管理员列表</span>
    </breadcrumb>
    <div>
         <el-table
      :data="tableData"
      style="width: 100%;border:1px solid #ebeef5;marginLeft:20px;marginTop:20px;marginBottom:30px">
      <el-table-column
        prop="user_name"
        label="姓名"
        width="180">
      </el-table-column>
      <el-table-column
        prop="create_time"
        label="注册日期"
        width="180">
      </el-table-column>
      <el-table-column
        prop="city"
        label="地址">
      </el-table-column>
      <el-table-column
        prop="admin"
        label="权限">
      </el-table-column>
    </el-table>
    </div>
     <el-pagination
      @current-change="handleCurrentChange"
      background
      :pager-coun="pageNub"
      layout="prev, pager, next"
      :total="100"
    ></el-pagination>
  </div>
</template>

<script>
import breadcrumb from '@/components/Breadcrumb.vue'
import { getmanData } from "../model/getData"; // ajax方法导入

export default {
  name: 'manDataman',
  components: {
    breadcrumb
  },
  data () {
    return {
      pageNub: 6,
      tableData: [],
    }
  },
  async created() {
    this.tableData = await getmanData(1);
  },
  methods: {
    async handleCurrentChange(val) {
      this.tableData = await getmanData(val);
    }
  }

}
</script>