</template>

<script>
export default {
  name: 'breadcrumb',
  data () {
    return {
      isshow: false
    }
  },
  methods: {
    fn1 () {
      this.$router.push('/home')
    },
    fn2 () {
      localStorage.setItem('isLogin', JSON.stringify({ 'code': 1 }))
    },
    enter () {
      this.isshow = true
    },
    leave () {
      this.isshow = false
    }
  }
}
</script>

