<template>
  <div class="login">
    <p>elm后台管理系统</p>
    <div class="isLogin">
      <el-input placeholder="用户名" v-model="input1" clearable class="inp" @input="userFn" :class="{active:select}"></el-input>
      <div class="errorLogin error_user" v-show="isUser" >请输入用户名</div>
      <el-input placeholder="密码" v-model="input2" clearable class="inp"
      @input="postFn">密码</el-input>
      <div class="errorLogin error_post" v-show="isPost">请输入密码</div>
      <el-row>
        <el-button type="primary" class="loginBtn" @click="onLogin">登录</el-button>
      </el-row>
      <p>温馨提示：</p>
      <p>未登录过的新用户，自动注册</p>
      <p>注册过的用户可凭账号密码登录</p>
    </div>
  </div>
</template>

<script>
import { getLoginData } from '../model/loginData' // ajax方法导入

export default {
  name: 'login',
  data () {
    return {
      input1: '',
      input2: '',
      isUser: false,
      isPost: false,
      select: false
    }
  },
  methods: {
    userFn () {
      if (this.input1) {
        this.isUser = false
        this.select = false
      } else {
        this.isUser = true
        this.select = true
      }
    },
    postFn () {
      if (this.input2) {
        this.isPost = false
      } else {
        this.isPost = true
      }
    },
    async onLogin () {
      if(this.input1 && this.input2){
        let loginData = await getLoginData({userName: this.input1, userPassword: this.input2}) // 请求登录数据验证权限
      this.$store.commit('changeOsLogin', loginData)
      localStorage.setItem("isLogin",JSON.stringify(loginData))
      if (JSON.parse(localStorage.getItem("isLogin")).code == "0") {
        this.$router.push('/home')
      } else {
        alert(loginData.msg)
      }
      }else{
        alert("请输入用户名和密码")
      }
    }
  }
}
</script>

<style lang="less" scoped>
.login {
  background-color: #324057;
  width: 100%;
  height: 100;
  &>p {
    font-size: 34px;
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -200px;
    margin-left: -120px;
  }
  .isLogin {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -105px;
    margin-left: -160px;
    padding: 25px;
    border-radius: 5px;
    text-align: center;
    width: 320px;
    height: 210px;
    background-color: #fff;
    .inp {
      width: 320px;
      height: 36px;
      margin-bottom:15px;
      padding-top:10px;
    }
    // .active{
    //   border:1px solid red ;
    // }
    .loginBtn {
      font-size: 16px;
      width: 320px;
      display: inline-block;
      line-height: 1;
      white-space: nowrap;
      cursor: pointer;
      margin-top:7px;
      padding: 10px 10px;
      border-radius: 4px;
      margin-bottom:10px;
    }
    .errorLogin{
        color: #ff4949;
        font-size: 12px;
        line-height: 1;
        padding-top: 4px;
        position: absolute;
    }
    .error_user{
        top: 74px;
        left: 25px;
    }
    .error_post{
        top: 134px;
        left: 25px;
    }
    p{
        font-size: 12px;
        color: red;
    }
  }
}
</style>
