import Vue from 'vue'
import Router from 'vue-router'
import home from './views/Home.vue'
import about from './views/About.vue'
import login from './views/Login.vue'
import manDatauser from './views/manData-user.vue'
import manDatacustomer from './views/manData-customer.vue'
import manDatafood from './views/manData-food.vue'
import addfood from './views/addData-foods.vue'
import addshop from './views/addData-shop.vue'
import manDataorder from './views/manData-order.vue'
import manDataman from './views/manData-man.vue'
import userDistribution from './views/userDistribution.vue'
import textEditing from './views/textEditing.vue'
import manageSet from './views/manageSet.vue'
import Explain from './views/Explain.vue'

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/about',
      name: 'about',
      component: about
    },
    {
      path: '/manDatauser',
      name: 'manDatauser',
      component: manDatauser
    },
    {
      path: '/manDatacustomer',
      name: 'manDatacustomer',
      component: manDatacustomer
    },
    {
      path: '/manDatafood',
      name: 'manDatafood',
      component: manDatafood
    },
    { // 添加食品
      path: '/addfood',
      name: 'addfood',
      component: addfood
    },
    { // 添加商铺
      path: '/addshop',
      name: 'addshop',
      component: addshop
    },
    {
      path: '/manDataorder',
      name: 'manDataorder',
      component: manDataorder
    },
    {
      path: '/manDataman',
      name: 'manDataman',
      component: manDataman
    },
    {
      path: '/userDistribution',
      name: 'userDistribution',
      component: userDistribution
    },
    {
      path: '/textEditing',
      name: 'textEditing',
      component: textEditing
    },
    {
      path: '/manageSet',
      name: 'manageSet',
      component: manageSet
    },
    {
      path: '/Explain',
      name: 'Explain',
      component: Explain
    }
  ]
})

router.beforeEach((to,from,next) => {
  if(to.fullPath !== '/' && JSON.parse(localStorage.getItem("isLogin")).code != "0"){
    next('/')
  }else{
    next()
  }
})

export default router
