<template>
  <div class="addfood">
    <breadcrumb>
      <span slot="breadone">添加数据&nbsp;/</span>
      <span slot="breadtwo">添加商品</span>
    </breadcrumb>
    <div class="titleOne">选择食品种类</div>
    <div class="foodTabOne">
      <span class="foodtitle" style="marginLeft:50px;fontSize:14px;color: #606266">食品种类</span>
      <el-select
        v-model="value"
        placeholder="请选择"
        style="width:520px;marginLeft:15px;marginTop:10px;marginBottom:30px"
      >
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-collapse accordion>
        <el-collapse-item>
          <template slot="title" style="margin:0 auto">
            <span>添加食品种类</span>
          </template>
          <el-form
            :label-position="labelPosition"
            label-width="120px"
            :model="formLabelAlign"
            size="small"
          >
            <el-form-item label="食品种类">
              <el-input v-model="formLabelAlign.type" style="width:520px"></el-input>
            </el-form-item>
            <el-form-item label="种类描述">
              <el-input v-model="formLabelAlign.describe" style="width:520px"></el-input>
            </el-form-item>
            <div style="marginLeft:120px">
              <el-row>
                <el-button type="primary" @click="pushBtn">提交</el-button>
              </el-row>
            </div>
          </el-form>
        </el-collapse-item>
      </el-collapse>
    </div>
    <div class="titleTwo">添加食品</div>
    <div class="foodTabTwo">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="食品名称" prop="name">
          <el-input v-model="ruleForm.name" style="width:550px;marginTop:10px"></el-input>
        </el-form-item>
        <el-form-item label="食品活动" prop="active">
          <el-input v-model="ruleForm.active" style="width:550px"></el-input>
        </el-form-item>
        <el-form-item label="食品详情" prop="details">
          <el-input v-model="ruleForm.details" style="width:550px"></el-input>
        </el-form-item>
      </el-form>
      <div class="pushPicture" style="margin-left:100px;margin-top:20px;position: relative;">
        <div class="picturetxt">商铺图片</div>
        <el-upload
          class="avatar-uploader"
          action="https://jsonplaceholder.typicode.com/posts/"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="imageUrl" :src="imageUrl" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon" style="border:1px #d9d9d9 dashed"></i>
        </el-upload>
      </div>
      <span class="foodtitle" style="marginLeft:30px;fontSize:14px;color: #606266">食品特点</span>
      <el-select
        v-model="value"
        placeholder="请选择"
        style="width:200px;marginLeft:15px;marginTop:10px;marginBottom:30px"
      >
        <el-option
          v-for="item in options2"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <div>
        <span class="foodtitle" style="marginLeft:30px;fontSize:14px;color: #606266">食品规格</span>
        <template>
          <el-radio v-model="radio" label="1" style="marginLeft:20px">单规格</el-radio>
          <el-radio v-model="radio" label="2">多规格</el-radio>
        </template>
      </div>
      <div style="margin-top:20px">
        <template>
          <span
            class="foodtitle"
            style="marginLeft:30px;fontSize:14px;color: #606266;margin-right:30px"
          >包装费</span>
          <el-input-number v-model="num1" @change="handleChange1" :min="1" :max="1000" label="描述文字"></el-input-number>
        </template>
      </div>
      <div style="margin-top:20px">
        <template>
          <span
            class="foodtitle"
            style="marginLeft:30px;fontSize:14px;color: #606266;margin-right:42px"
          >价格</span>
          <el-input-number v-model="num2" @change="handleChange2" :min="1" :max="1000" label="描述文字"></el-input-number>
        </template>
      </div>
      <div style="marginLeft:120px;marginTop:20px;marginBottom:20px">
        <el-row>
          <el-button type="primary" @click="pushBtn2">确认添加食品</el-button>
        </el-row>
      </div>
    </div>
  </div>
</template>
    </div>
  </div>
</template>

      </div>
    </div>
    <div>
    </div>
  </div>
</template>

<script>
import breadcrumb from "@/components/Breadcrumb.vue";
import { getfoodTypeData } from "../model/getData"; // ajax方法导入

export default {
  name: "addfood",
  data() {
    return {
      labelPosition: "right",
      formLabelAlign: {
        type: "",
        describe: ""
      },
      options: [],
      options2: [
        {
          value: "选项1",
          label: "新品"
        },
        {
          value: "选项2",
          label: "招牌"
        }
      ],
      value: "",
      imageUrl: "",
      ruleForm: {
        name: "",
        active: "",
        details: ""
      },
      rules: {
        name: [
          { required: true, message: "请输入食品名称", trigger: "blur" },
          { min: 1, max: 10, message: "长度在 1 到 10 个字符", trigger: "blur" }
        ],
        active: [
          { required: true, message: "请输入食品活动", trigger: "blur" },
          { min: 1, max: 10, message: "长度在 1 到 10 个字符", trigger: "blur" }
        ],
        details: [
          { required: true, message: "请输入食品详情", trigger: "blur" },
          { min: 1, max: 10, message: "长度在 1 到 10 个字符", trigger: "blur" }
        ]
      },
      radio: "1",
      num1: 1,
      num2: 20
    };
  },
  components: {
    breadcrumb
  },
  async created() {
    this.options = await getfoodTypeData();
  },
  methods: {
    pushBtn() {},
    pushBtn2() {},
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        alert("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        alert("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    handleChange1(value) {
      console.log(value);
    },
    handleChange2(value) {
      console.log(value);
    }
  }
};
</script>

<style lang="less" scoped>
.titleOne {
  font-size: 18px;
  margin-left: 450px;
  margin-top: 20px;
  margin-bottom: 10px;
}
.titleTwo {
  font-size: 18px;
  margin-left: 465px;
  margin-top: 20px;
  margin-bottom: 10px;
}
.foodTabOne {
  width: 680px;
  border: 1px solid #eaeefb;
  margin-left: 200px;
}
.foodTabTwo {
  width: 680px;
  border: 1px solid #eaeefb;
  margin-left: 200px;
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409eff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .picturetxt {
    position: absolute;
    top: 0;
    left: -70px;
    font-size: 14px;
    color: #48576a;
  }
}
.el-collapse-item__header{
    span{
        margin-left:260px;
        color: #999;
    }
}
</style>
