<template>
  <div class="manDatauser">
    <breadcrumb>
    <span slot="breadone">数据管理&nbsp;/</span>
    <span slot="breadtwo">用户列表</span>
    </breadcrumb>
    <div class="manageTable" id="a">
      <el-table
        ref="singleTable"
        :data="tableData"
        highlight-current-row
        style="width:1000px;border:1px solid #ebeef5;marginLeft:20px;marginTop:20px;marginBottom:30px"
        class="manTab"
      >
        <el-table-column type="index" label="#" width="120" id="a"></el-table-column>
        <el-table-column property="registe_time" label="注册日期" width="220" id="a"></el-table-column>
        <el-table-column property="username" label="用户姓名" width="220"></el-table-column>
        <el-table-column property="city" label="注册地址"></el-table-column>
      </el-table>
      <el-pagination
        @current-change="handleCurrentChange"
        background
        :pager-coun="pageNub"
        layout="prev, pager, next"
        :total="100"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import breadcrumb from '@/components/Breadcrumb.vue'
import { getUserData } from '../model/getData' // ajax方法导入

export default {
  name: 'manDatauser',
  components: {
    breadcrumb
  },
  data () {
    return {
      pageNub: 6,
      isborder: true,
      tableData: []
    }
  },
  async created () {
    this.tableData = await getUserData(1)
  },
  methods: {
    async handleCurrentChange (val) {
      this.tableData = await getUserData(val)
    }
  }
}
</script>

<style lang="less" scoped>
.manageTable {
  position: absolute;
  top: 80px;
  left: 30px;
}
.manTab {
  padding-left: 18px;
  padding-right: 18px;
  box-sizing: border-box;
  text-overflow: ellipsis;
}
</style>
