<template>
  <div class="manDataorder">
    <breadcrumb>
    <span slot="breadone">数据管理&nbsp;/</span>
    <span slot="breadtwo">订单列表</span>
    </breadcrumb>
      <el-table :data="tableData" style="width: 90%;marginLeft:20px;marginTop:20px;border:1px solid #ebeef5" >
        <el-table-column type="expand" >
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="用户名">
                <span>{{ props.row.user_id }}</span>
              </el-form-item>
              <el-form-item label="收货地址">
                <span>{{ props.row.address_id }}</span>
              </el-form-item>
              <el-form-item label="店铺地址">
                <span>{{ props.row.address_id }}</span>
              </el-form-item>
              <el-form-item label="店铺名称">
                <span>{{ props.row.restaurant_name }}</span>
              </el-form-item>
              <el-form-item label="店铺 ID">
                <span>{{ props.row.restaurant_id }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="订单 ID" prop="unique_id"></el-table-column>
        <el-table-column label="总价格" prop="total_amount"></el-table-column>
        <el-table-column label="订单状态" prop="status_bar.title"></el-table-column>
      </el-table>
    <el-pagination
      @current-change="handleCurrentChange"
      background
      :pager-coun="pageNub"
      layout="prev, pager, next"
      :total="100"
    ></el-pagination>
  </div>
</template>

<script>
import breadcrumb from '@/components/Breadcrumb.vue'
import { getOrderData } from "../model/getData"; // ajax方法导入

export default {
  name: 'manDataorder',
  components: {
    breadcrumb
  },
  data () {
    return {
      pageNub: 6,
      tableData: [],
    }
  },
  async created() {
    this.tableData = await getOrderData(1);
  },
  methods: {
    async handleCurrentChange(val) {
      this.tableData = await getOrderData(val);
    }
  }
}
</script>